<template>
<div class="mapstore">
   <baidu-map :center="keyword" >
    <bm-view class="map" ></bm-view>
    <!-- <bm-local-search :keyword="keyword" :bounds="bounds" :auto-viewport="true" :panel="false"></bm-local-search> -->
    <!-- <bm-polygon :path="polygonPath"></bm-polygon> -->
    
    <bm-marker 
        v-for="(maitem,index) in markers" :key="maitem.id"
        :position="{lng: maitem.lng, lat: maitem.lat}" 
        :dragging="false" 
        @click="infoWindowOpen(maitem,index)"
        animation="BMAP_ANIMATION_BOUNCE"
        :icon="{url: maitem.isActive ? iconActive : icon, size: {width: 40, height: 40}}"
    >
    <!--  -->
        <!-- <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">我爱北京天安门</bm-info-window> -->
    </bm-marker>
    <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true" showAddressBar='true'></bm-geolocation>
   </baidu-map>
   <div :class="{mapfooter:true,active:isButton}">       
    <div>
        <p><yd-button size="large" type="hollow"  @click.native="cancel" >取消</yd-button></p>
        
    </div>
    <div>
        <p><yd-button size="large" type="primary" @click.native="ok">确认</yd-button></p>
    </div>
   </div>
   </div>
</template>

<script>
export default {
    data () {
        return {
            iconActive:require("./img/cur.png"),
            icon:require("./img/curout.png"),            
            cartIds: false, 
            keyword:"中国",
            isButton:false,
            pStart: {
                lng: 116.294625,
                lat: 39.961627
            },
            pEnd: {
                lng: 116.357474,
                lat: 39.988609
            },
            markers:[]
        }
    },
    mounted () {
        this.getDefaultStore();
        this.setMapStotre();
    },
    methods: {
        setMapStotre(){
            const _this = this;
            this.$wx.getLocation({
                type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                success: function (res) {
                    _this.keyword={lat:res.latitude,log:res.longitude}                    
                }
            });
        },
        infoWindowOpen(res,index){
            this.isButton = true;
            this.markers = this.markers.map(r=>({...res,...{isActive:false}}));
            this.markers[index] = {...res,...{isActive:!res.isActive}};
        },
        // 获取默认门店
        getDefaultStore () {
            const _this = this;
            this.$api.getStroe({}).then(res => {
                if (res.status) {
                   _this.markers = res.data.map(r=>
                   ({...r,...{lng:parseFloat(`${r.coordinate}`.split(",")[1]),lat:parseFloat(`${r.coordinate}`.split(",")[0])}})
                   );
                   console.log(_this.markers)
                }
            })
        },
        bounds () {
            const {pStart, pEnd} = this
            return {
                sw: {lng: pStart.lng, lat: pStart.lat},
                ne:{lng: pEnd.lng, lat: pEnd.lat}
            }
        },
        polygonPath () {
            const {pStart, pEnd} = this
            return [
                {lng: pStart.lng, lat: pStart.lat},
                {lng: pEnd.lng, lat: pStart.lat},
                {lng: pEnd.lng, lat: pEnd.lat},
                {lng: pStart.lng, lat: pEnd.lat}
            ]
        },
        ok(){
            var map = this.markers.map(r=>r.isActive ? r : false).filter(r=>r)[0];
            if(!map){
                this.$dialog.toast({
                    mes: '请选择门店',
                    timeout: 1500,
                    icon: 'error'
                });
                return false;
            }
            window.localStorage.setItem("storeMap",JSON.stringify(map))
            window.history.go(-1);
        },
        cancel(){
            window.history.go(-1);
        }
      
    }
}
</script>

<style>
    .ordercoupon{
        background-color: #fff;
    }
   .map{width:100%;height:calc(100vh);}
   .mapstore{ position: fixed; left: 0; top: 0; z-index: 4; width: 100%; height: 100%;}
   .mapfooter{
       position: absolute;left: 0; bottom: -2rem; z-index: 400;  width: 100%; background: #fff; border-top: solid 1px #ccc; padding: .2rem 0;
   }
   .mapfooter > div{ float: left; width: 50%;}
   .mapfooter > div .yd-btn-block{ margin: 0; display: block; }
   .mapfooter > div p{ margin: 0 .2rem; display: block; }
   .mapfooter > div:nth-child(1) p{margin-right: .1rem;}
   .mapfooter > div:nth-child(2) p{ margin-left: .1rem;}
   .mapfooter.active{bottom:0;}
</style>



